/*	SERVICES TABS
/*-----------------------------------------------------------------------------------*/

.tab-block {
    display: none
}

.services .etabs {
    padding: 0;
    overflow: inherit;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.services, .services .etabs {
    text-align: center
}

.services .tab {
    display: inline-block;
    position: relative;
}

.services .tab .pin {
    content: "";
    width: 9px;
    height: 9px;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    background: #dadada;
    display: inline-block;
    z-index: 200;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    -moz-transition: all 200ms ease-in;
}

.services .tab .root:before {
    content: "";
    display: inline-block;
    border-bottom: 1px solid #dadada;
    width: 50%;
    height: 0px;
    position: absolute;
    top: 12px;
    left: 0;
}

.services .tab .root:after {
    content: "";
    display: inline-block;
    border-bottom: 1px solid #dadada;
    width: 50%;
    height: 0px;
    position: absolute;
    top: 12px;
    right: 0;
}

/* Firefox Fix */

@-moz-document url-prefix() {
    .services .tab .root:before, .services .tab .root:after {
        margin-top: 1px
    }
}

.services .tab:first-child .root:before {
    display: none
}

.services .tab:last-child .root:after {
    display: none
}

.services .tab a {
    display: block
}

.services .tab .icon {
    display: table;
    margin: 10px auto 0;
    position: relative;
}

.services .tab .icon i.icn {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    width: auto;
    height: 60px;
    color: #bababa;
    line-height: 1;
    font-size: 50px;
    text-transform: uppercase;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    -moz-transition: all 200ms ease-in;
}

.services .tab:hover .icon i.icn, .services .tab.active .icon i.icn {
    color: #3f8dbf
}

.services .tab:hover .pin, .services .tab.active .pin {
    background-color: #3f8dbf
}

.services .tab h4 {
    text-transform: uppercase;
    font-size: 14px;
    color: #bababa;
    font-weight: 800;
    -webkit-transition: all 200ms ease-in;
    -o-transition: all 200ms ease-in;
    -moz-transition: all 200ms ease-in;
}

