.steps {
    margin: 0 auto;
    width: 700px;
    overflow: hidden;
    list-style: none;
    padding: 0;
    position: relative;
}

.timeline-border {
    position: absolute;
    top: 0;
    left: 50%;
    border-left: 1px solid #dadada;
    height: 100%;
}

.step {
    width: 700px;
    float: right;
    text-align: left;
    margin: 0 0 10px;
    padding: 10px 0 0 405px;
    position: relative;
}

.step.even {
    float: left;
    text-align: right;
    padding: 10px 405px 0 0;
}

.step h5 {
    color: #3f8dbf
}

