/*	DEMO ONLY
/*-----------------------------------------------------------------------------------*/

a.changecolor {
    cursor: pointer;
}

.about-header {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    background-image: url('./../images/about1.jpg');
    height: 50vh;
    width: 100%;
    color: white;
    background-color: #35cbe575;
    background-blend-mode: multiply;
    background-size: cover;
    display: grid;
    place-items: center;
    padding-top: 5px;
}

.about-item {
    width: 33%;
    padding-left: 20px;
    border-right: 1px dotted var(--primary);
}

.logoImage {
    height: 40px;
}

.who-we-are {
    width: 75%;
}

.who-we-are-image {
    width: 25%;
    background-image: url('./../images/who.jpg');
    display: block;
    height: auto;
    background-blend-mode: multiply;
    background-size: cover;
    display: grid;
    place-items: center;
    /* padding-top: 5px; */
}

.primary {
    color: var(--primary);
}

.secondary {
    color: var(--secondary);
}

.tertiary {
    color: var(--background);
}

.question_tag{
    height:45px;
    width: 45px;
    margin-right: 20px;
    border-radius: 50%;
    background-color: var(--background);
    color: var(--primary);
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    justify-items: center;
    font-size: 25px;
  }
  .ans_tag{
    height:15px;
    width: 15px;
    margin-right: 0px;
    border-radius: 50%;
    background-color: var(--primary);
    color: var(--secondary);
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    justify-items: center;
    font-size: 5px;
  }

@media screen and (max-width: 800px) {
    .about-header {
        display: none
    }
    .about-item {
        width: 100%;
        padding-left: 0px;
        border-top: 1px dotted var(--primary);
        border-right: unset;
    }
    .container-fluid {
        margin-left: 10px;
        margin-right: 10px;
    }
    .logoImage {
        height: 20px;
    }
    .who-we-are {
        width: 100%;
    }
    .who-we-are-image {
        display: none;
    }
}